export default function (router) {
  router
  .map({
    '/doc': {
      name: 'doc',
      component: require('./pages/Doc'),
      subRoutes: {
        '/button': {
          name: 'button',
          component: require('./pages/button')
        }
      }
    }
  })
  .redirect({
    '*': '/doc/button'
  })
}



